.mainView {
    width: 100%;
    flex: 1;
    position: relative;
    //overflow: hidden;
}

.mainView > div {
    overflow: hidden;
}

ul {
    list-style: none;
    width: 100%;
    padding: 0;
    margin: 0;
}

li {
    border-bottom: 1px solid $grayline;
    padding: 15px 0 15px 0;
    color: $textGray;
    font-size: 0.8em;
}

li:hover {
    color: $textHover;
    cursor: pointer;
}

.RightPanel p {
    margin: 0;
    background-color: $primaryDarkColor;
    padding: 10px 0 10px 0;
    font-size: 0.9em;
    position: absolute;
    bottom: 0;
    width: 100%;
    color: $textHover;
    font-weight: bolder;
}

.panelIcon {
    text-align: center;
    margin: auto;
    font-size: 2.5em;
    display: block;
    padding-bottom: 5px;
}

.screen {
    width: 100%;
    min-height: 100vh;
}

.ActiveRitem {
    color: $textHover;
}

